/**Generates a random color */
export function RandomColor(): string {
    var keys = Object.keys(ColorCodes);

    //@ts-expect-error
	return ColorCodes[keys[keys.length * Math.random() << 0]];
}

/** 
 * Converts a heximal string to a byte array 
 * @param hex Heximal string 
 */
export function ConvertHexToByteArray(hex: string) {
    const bytes: number[] = [];
    for (let c = 0; c < hex.length; c += 2)
        bytes.push(parseInt(hex.substr(c, 2), 16));
    bytes.forEach(function (byte: number, index: number) {
        if (isNaN(byte)) bytes.splice(index, 1);
    });
    return bytes;
}

/** 
 * Converts byte array to a heximal string.
 * @param bytes Array with bytes
 */
export function ConvertByteArrayToHex(bytes: number[]) {
    for (var hex = [], i = 0; i < bytes.length; i++) {
        var current = bytes[i] < 0 ? bytes[i] + 256 : bytes[i];
        hex.push((current >>> 4).toString(16));
        hex.push((current & 0xF).toString(16));
    }
    return hex.join("");
}

/** 
 * Converts a heximal string to a byte array (but in a fixed and accurate way).
 * @param hex Heximal string 
 */
export function FixedHexToRgbArray(hex: string): number[] | null {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? [parseInt(result[1], 16), parseInt(result[2], 16), parseInt(result[3], 16)]: null;
}

/**
 * Animates a heximal color string.
 **/
export function AnimateHeximalColor(from: string, to: string, easing: EasingName, duration: number, cb: (color: string) => void) {

	if (typeof from === "undefined") throw new Error("Cannot animate heximal color since the 'from' argument has not been filled.");
	if (typeof to === "undefined") throw new Error("Cannot animate heximal color since the 'to' argument has not been filled.");
	if (typeof cb !== "function") throw new Error("Cannot animate heximal color since the 'cb' argument has not been specified as a function.");

	const now: number = Date.now();

	const currentColor: number[] | null = FixedHexToRgbArray(from);
	const endColor: number[] | null = FixedHexToRgbArray(to);

	if (endColor === null || currentColor === null) throw new Error("Cannot animate background color as it contains an invalid value.");

	const updateTick = () => {
		const elapsedTime: number = Date.now() - now;

		const r: number = Easings[easing](elapsedTime, 0, endColor[0] - currentColor[0], duration);
		const g: number = Easings[easing](elapsedTime, 0, endColor[1] - currentColor[1], duration);
		const b: number = Easings[easing](elapsedTime, 0, endColor[2] - currentColor[2], duration);

		const byteArray = [currentColor[0] + r, currentColor[1] + g, currentColor[2] + b];
		const convertedHeximalString = ConvertByteArrayToHex(byteArray);

		cb("#" + convertedHeximalString);

		if (elapsedTime < duration) {
			window.requestAnimationFrame(updateTick);
		} else {
			cb(to);
		}
	}

	updateTick();
}